<style scoped>

</style>
<template>
  <div>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="订单编号">
          <Input v-model="formData.order_no" placeholder="请输入订单编号" class="filter-input"></Input>
        </FormItem>
        <FormItem label="状态">
          <Select class="filter-input" v-model="formData.status">
            <Option label="待审核" :value="0"></Option>
            <Option label="审核流程中" :value="1"></Option>
            <Option label="审核通过" :value="2"></Option>
            <Option label="退回" :value="-1"></Option>
          </Select>
        </FormItem>

      </Form>
    </Row>
    <div class="filter-btn">
      <span class="span">操作 </span>
      <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
      <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
      <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>

    </div>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      formData: {},
      load: false,
      space: [],
      listData: {},
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '订单编号',
          align: 'center',
          key: 'order_no'
        },
        {
          title: '签署团队',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.team.company)
          }
        },
        {
          title: '订单金额',
          align: 'center',
          key: 'total_price'
        },
        {
          title: '创建时间',
          align: 'center',
          key: 'create_time'
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '待审核' : params.row.status === 1 ? '审核流程中' : params.row.status === 2 ? '审核通过' : '退回')
          }
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginLeft: '5px'
                },
                on: {
                  click: () => {
                    this.detail(params.row.id)
                  }
                }
              }, '详细'),
              h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginLeft: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    add () {
      this.$router.push({
        name: 'orderEdit'
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    detail (id) {
      this.$router.push({
        name: 'orderDetail',
        params: { id: id }
      })
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.order_no + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  }
}
</script>
